/* General Styling */
h2 {
  width: 100%;
  margin: 0px;
  text-align: center;
  font-family: "Josefin Sans";
  font-size: 30px;
  font-weight: 700;
  color: rgb(51, 51, 51);
}

h3 {
  margin: 0;
  padding-bottom: 5px;
  font-family: "Josefin Sans";
  font-weight: 600;
  font-size: 1.2em;
}

h4 {
  margin: 0;
  font-weight: 400;
  padding-bottom: 10px;
  font-family: "Josefin Sans";
  font-size: 1em;
  color: rgb(63, 63, 63);
}

h5 {
  margin: 0;
  font-family: "Josefin Sans";
  font-weight: 400;
  font-style: italic;
  font-size: 12px;
  color: rgb(240, 106, 44);
}

.header {
  display: none;
}

.title-wrapper {
  width: 100%;
  margin-bottom: 3vh;
}

.blocks {
  width: 90%;
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-bottom: 3vh;
}

.write-up {
  margin-top: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3em;
  font-family: "Raleway";
  text-align: justify;
}

.logoimg {
  width: 100%;
  border-radius: 10px;
  margin: 0px;
  padding: 0px;
}

.logo,
.info {
  padding: 1vh 2vw 1vh 2vw;
}

@media only screen and (min-width: 768px) {
  h3 {
    font-size: 1.3em;
  }

  h4 {
    font-size: 1em;
  }

  h5 {
    font-size: 12px;
  }

  .header {
    height: 100vh;
    width: 100%;

    background-image: url(/Darryl-Leong/static/media/background-ipad.f37428d0.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .header-wrapper {
    height: inherit;
    width: inherit;
    background-color: rgba(0, 0, 0, 0.3);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .header-text-first {
    text-align: left;
    color: white;
    font-size: 50px;
    font-style: italic;
    font-weight: 300;
    font-family: "Josefin Sans";
    padding: 10px 0px 10px 0px;
  }

  .header-text-second {
    margin: 0;
    padding: 0;

    color: rgb(255, 255, 255);
    font-size: 60px;
    font-weight: 700;
    /* font-style: italic; */

    padding: 30px 30px 20px 30px;
    border: none;
    background-color: rgb(106, 181, 251);
    /* line-height: 1.1em; */
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }

  .sm-wrapper {
    position: absolute;
    bottom: 50px;
  }

  .sm-icon {
    margin: 5px;
    padding: 5px;
    color: rgb(63, 150, 200);
    -webkit-transition: 0.5s;
    transition: 0.5s;
    background-color: white;
    border-radius: 50%;
  }

  .sm-icon:hover {
    cursor: pointer;
    color: white;
    background-color: rgb(63, 150, 200);
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }

  .title-wrapper {
    width: 100%;
    margin-bottom: 3vh;
  }

  .blocks {
    width: 90%;
    display: flex;
    flex-direction: row;
    text-align: left;
    padding-bottom: 3vh;
  }

  .write-up {
    margin-top: 10px;
    font-weight: 400;
    font-size: 15px;
    line-height: 1.3em;
    font-family: "Raleway";
    text-align: justify;
  }

  .logoimg {
    width: 35vw;
    border-radius: 10px;
    margin: 0px;
    padding: 0px;
  }

  .logo,
  .info {
    padding: 1vh 2vw 1vh 1vw;
  }
}

@media only screen and (min-width: 1000px) {
  h3 {
    margin: 0;
    padding-bottom: 5px;

    font-family: "Josefin Sans";
    font-weight: 600;
    font-size: 1.5em;
  }

  h4 {
    margin: 0;
    font-weight: 400;
    padding-bottom: 10px;
    font-family: "Josefin Sans";
    color: rgb(63, 63, 63);
  }

  h5 {
    margin: 0;
    font-family: "Josefin Sans";
    font-weight: 400;
    font-style: italic;
    font-size: 15px;
    color: rgb(240, 106, 44);
  }

  .header {
    background-image: url(/Darryl-Leong/static/media/background.d94595fa.JPG);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: top;
  }

  .title-wrapper {
    width: 50%;
    /* padding: 50px 0px 50px 0px; */
  }

  .blocks {
